* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #000;
  font-family: Arial, sans-serif;
}

.header {
  display: flex;
  flex-direction: column;
  width: 100%;
  position: fixed;
  z-index: 1100;
  left: auto;
  right: 0px;
  color: rgb(255, 255, 255);
  background-color: rgb(30, 8, 21);
  box-shadow: none;
  top: 0px;
  padding-right: 0px !important;
}

.header-Container{
  display: flex;
  align-items: center;
  gap: 36px;
  -webkit-box-pack: center;
  justify-content: center;
  padding: 12px 10px;
}

.logo-image{
  color: transparent;
    max-width: 100%;
    height: auto;
}

.search-box {
  display: flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;
  width: 100%;
  max-width: 420px;
  border: 1px solid rgba(233, 232, 235, 0.2);
  border-radius: 8px;
  padding: 12px 16px;
}
.search-box span{
  margin: 0px;
  font-size: 16px;
  font-weight: 500;
  line-height: 120%;
  font-style: normal;
}


.login-box{
  border: 1px solid rgba(233, 232, 235, 0.2);
  border-radius: 8px;
  padding: 12px 16px;
  height: 100%;
  max-height: 46px;
  align-items: center;
  text-align: center;
  display: flex;
  cursor: pointer;
  gap: 8px;
}
.login-box span{
  margin: 0px;
  font-size: 16px;
  font-weight: 500;
  line-height: 120%;
  font-style: normal;
  color: rgb(233, 232, 235);
}
.input-box{
  border: 1px solid rgba(233, 232, 235, 0.2);
  border-radius: 8px;
  padding: 12px 16px;
  height: 100%;
  max-height: 46px;
  -webkit-box-align: center;
  align-items: center;
  text-align: center;
  display: flex;
  cursor: pointer;
  gap: 8px;
}
.input-box span{
  margin: 0px;
  font-size: 16px;
  font-weight: 500;
  line-height: 120%;
  font-style: normal;
  color: rgb(233, 232, 235);
}
.svg-select{
  user-select: none;
    display: inline-block;
    fill: currentcolor;
    flex-shrink: 0;
    font-size: 1.5rem;
    height: 16px;
    width: 16px;
    color: rgb(255, 255, 255);
    cursor: pointer;
    padding-right: 0px !important;
    transition: fill 200ms cubic-bezier(0.4, 0, 0.2, 1);
}

.free-call-button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  position: relative;
  cursor: pointer;
  user-select: none;
  vertical-align: middle;
  font-size: 20px;
  font-weight: 700;
  line-height: 120%;
  font-style: normal;
  text-transform: none;
  font-family: sans-serif;
  min-width: 64px;
  color: rgb(255, 72, 179);
  width: 318px;
  box-shadow: rgba(255, 104, 192, 0.48) 0px 4px 10px;
  outline: 0px;
  border-width: 0px;
  margin: 0px;
  text-decoration: none;
  padding: 6px 8px;
  background: linear-gradient(90deg, rgb(254, 205, 61), rgb(255, 241, 198), rgb(255, 104, 192));
  border-radius: 8px;
  gap: 8px;
}
.free-call-button span{
  margin: 0px;
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    text-transform: none;
    line-height: 150%;
    color: rgb(41, 15, 30);
}
.menu-button{
  display: none;
}


@media (max-width: 1024px) {
  .search-box {
      width: auto;
      max-width: none;
  }
}

@media (max-width: 900px) {
  .free-call-button {
      display: none;
  }
  .search-box {
    display: flex;
    align-items: center;
    gap: 8px;
    cursor: pointer;
    width: auto;
    max-width: auto;
    border: none;
    border-radius: 0;
    padding: 0;
  }
  .search-box span{
    display: none;
  }

  .login-box{
    display: none;
  }
  .menu-button{
    display: inline-flex;
    align-items: center;
    justify-content: center;
    position: relative;
    background-color: transparent;
    cursor: pointer;
    vertical-align: middle;
    text-align: center;
    font-size: 1.5rem;
    color: rgba(0, 0, 0, 0.54);
    outline: 0px;
    border-width: 0px;
    margin: 0px;
    text-decoration: none;
    flex: 0 0 auto;
    padding: 8px;
    border-radius: 50%;
  }
}

/* footer css */
.footer{
  background-color: rgb(30, 8, 21);
  overflow: hidden;
  width: 100%;
  height: 890px;
  align-items: end;
  position: relative;
  margin-top: 120px;
}

.footer-container{
  display: flex;
  justify-content: center;
  width: 100%;
}
.footer-inner{
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-width: 1244px;
  text-align: center;
  align-items: center;
  position: absolute;
  z-index: 3;
  top: 64px;
  width: 100%;
}